<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="@/assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            USER LIST
          </v-card-text>
        </v-img>
      </v-card>

      <v-row class="mt-2">
        <v-col cols="12" md="12">
          <v-card>
            <div class="pl-5 pr-5">
              <v-row>
                <v-col cols="12" sm="12">
                  <!-- <v-row>
                    <v-col>
                      <v-row>
                        <v-col cols="12" md="8" sm="8" class="pl-10">
                          <v-text-field
                            v-model="username"
                            label="Search by Name"
                          ></v-text-field>
                        </v-col>

                        <v-col cols="12" md="4" sm="4" class="pt-10">
                          <v-btn small @click="searchUsername"> Search </v-btn>
                        </v-col>
                      </v-row>
                    </v-col>

                    <v-col cols="12" md="4" class="text-center">
                      <v-tooltip left>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            color="primary"
                            to="/addMember"
                            class="text-decoration-none"
                            dark
                            fab
                            v-bind="attrs"
                            v-on="on"
                          >
                            <v-icon>mdi-plus</v-icon>
                          </v-btn>
                        </template>
                        <span>ADD USER</span>
                      </v-tooltip>
                    </v-col>
                  </v-row> -->
                  <v-card-title>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          color="primary"
                          to="/addMember"
                          class="text-decoration-none"
                          dark
                          fab
                          v-bind="attrs"
                          v-on="on"
                          small
                        >
                          <v-icon>mdi-plus</v-icon>
                        </v-btn>
                      </template>
                      <span>ADD USER</span>
                    </v-tooltip>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>

                  <v-data-table
                    :headers="headers"
                    :items="members"
                    :search="search"
                    :hide-default-footer="false"
                  >
                    <template slot="item.numbering" slot-scope="props">
                      {{ props.index + 1 }}
                    </template>

                    <template v-slot:[`item.status`]="{ item }">
                      <v-chip :color="getStatusColor(item.status)" dark small>
                        <tr>
                          {{
                            item.status.toLowerCase()
                          }}
                        </tr>
                      </v-chip>
                    </template>

                    <template v-slot:[`item.roles`]="{ item }">
                      <v-chip :color="getRolesColor(item.roles)" dark small>
                        <tr>
                          {{
                            item.roles[0].name.toLowerCase()
                          }}
                        </tr>
                      </v-chip>
                    </template>

                    <template v-slot:[`item.actions`]="{ item }">
                      <v-chip
                        dark
                        small
                        color="teal lighten-2"
                        @click="editMember(item.id)"
                        class="mr-1"
                      >
                        <!-- <v-icon>mdi-file-document-edit</v-icon> -->
                        <span>EDIT</span>
                      </v-chip>
                      <v-chip
                        dark
                        small
                        color="red darken-1"
                        @click="deleteMember(item.id)"
                      >
                        <!-- <v-icon>mdi-delete</v-icon> -->
                        <span>DELETE</span>
                      </v-chip>
                    </template>
                  </v-data-table>
                </v-col>
              </v-row>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import MemberDataService from "../../services/MemberDataService";
export default {
  name: "member-list",
  data() {
    return {
      members: [],
      // userLevel: [],
      // username: "",
      search: "",
      headers: [
        {
          text: "NO.",
          align: "start",
          sortable: false,
          value: "numbering",
          // class: "blue lighten-5",
        },
        {
          text: "NAME",
          align: "start",
          sortable: false,
          value: "username",
          // class: "blue lighten-5",
        },
        {
          text: "EMAIL",
          value: "email",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "DEPARTMENT",
          align: "center",
          value: "dept",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "BRANCH",
          align: "center",
          value: "branch",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "STATUS",
          align: "center",
          value: "status",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "ACCESS",
          align: "center",
          value: "roles",
          sortable: false,
          // class: "blue lighten-5",
        },
        {
          text: "ACTIONS",
          align: "center",
          value: "actions",
          sortable: false,
          // class: "blue lighten-5",
        },
      ],
    };
  },

  computed: {
    memberRoles: function () {
      // return this.members.filter(function(item) {
      //   return item.id == 2;
      // });

      return this.members.filter((item) => [2].includes(item.id));
    },

    // filteredMembers: function () {
    //   return this.members.filter((member) => {
    //     // return member.branch.match("KEL");
    //     return member.branch.match(this.$store.state.auth.user.branch);
    //   });
    // },
  },

  methods: {
    retrieveMembers() {
      MemberDataService.getAll()
        .then((response) => {
          this.members = response.data.map(this.getDisplayMember);
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveMembers();
    },

    getRolesColor(roles) {
      if (roles[0].name == "ADMIN") return "red";
      else if (roles[0].name == "MANAGER") return "orange";
      else if (roles[0].name == "BRANCH MANAGER") return "indigo";
      else return "grey";
    },

    getStatusColor(status) {
      if (status == "Inactive") return "green lighten-2";
      else return "green darken-3";
    },

    // retrieveRoles() {
    //   MemberDataService.getAllRoles()
    //     .then((response) => {
    //       this.userLevel = response.data;
    //       console.log(response.data);
    //     })
    //     .catch((e) => {
    //       console.log(e);
    //     });
    // },

    // filters() {
    //   return this.data.members(
    //     (item) => item[0].roles[0].user_roles.userId === "members"
    //   );
    // },

    removeAllMembers() {
      MemberDataService.deleteAll()
        .then((response) => {
          console.log(response.data);
          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    searchUsername() {
      MemberDataService.findByUsername(this.username)
        .then((response) => {
          this.members = response.data.map(this.getDisplayMember);
          console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    editMember(id) {
      this.$router.push({ name: "member-details", params: { id: id } });
    },

    deleteMember(id) {
      MemberDataService.delete(id)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });

          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDisplayMember(member) {
      return {
        id: member.id,
        username:
          member.username.length > 30
            ? member.username.substr(0, 30) + "..."
            : member.username,
        email:
          member.email.length > 30
            ? member.email.substr(0, 30) + "..."
            : member.email,
        status: member.active ? "Active" : "Inactive",
        roles: member.roles,
        branch: member.branch,
        dept: member.dept,
      };
    },
  },
  mounted() {
    this.retrieveMembers();
    // this.retrieveRoles();
  },
};
</script>
